* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

body {
  line-height: 1;
}

.header {
  height: 100px;
  /* all_nav end */
}
.header .inner {
  max-width: 1280px;
  width: calc(100% - 30px);
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .nav .navAll .gnb {
  display: flex;
  gap: 30px;
}
.header .nav_open {
  position: relative;
  z-index: 99;
}
.header .nav_open img {
  width: 30px;
}
.header .all_nav {
  width: 54%;
  height: 100vh;
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  background-color: rosybrown;
}
.header .all_nav.on {
  display: block;
}

