@charset "utf-8";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: inherit;
}
li{
    list-style: none;
}
body {
    line-height: 1;
}
.header {
    height: 100px;
    .inner {
        max-width: 1280px;
        width: calc(100% - 30px);
        height: 100%;
        margin: 0 auto;      
        display: flex;  
        align-items: center;
        justify-content: space-between;
    }
    .nav {
        .navAll {
            .gnb {
                display: flex;
                gap: 30px;
            }
        }
    }
    .nav_open {
        position: relative;
        z-index: 99;
        img {
            width: 30px;
        }
    }
    
    .all_nav {
        width: 54%;
        height: 100vh;
        display: none;
        position: fixed;
        right: 0;
        top: 0;
        background-color: rosybrown;
        &.on {
            display: block;
        }
    } /* all_nav end */
     
}/* header end */
